@import url(https://fonts.googleapis.com/css?family=Roboto:300,400);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App-logo {
 object-fit: contain;
 width: 200px;
}
span.price-new {
  color: #999;
  font-weight: 400;
  font-size: 20px;
}
.img-wrap img {
  width: 100%;
  height: 200px;
  object-fit: contain;
  padding: 5px 0;
}
h5{
  color: #4f4f4f;
}
.outer_cart .card, .outer_cart.col-lg-8.card  {
  box-shadow: 0 2px 5px 0 rgb(183 183 183 / 20%), 0 2px 15px 0 rgb(0 0 0 / 4%);
  border: none;
}
h2.cart-item-top {
  font-size: 20px;
  padding-top: 30px;
  color: #6c757d;
}
.inner_cheackout.wrap {
  background: #f7f7f7;
  padding: 30px;
  box-shadow: 0 2px 5px 0 rgb(183 183 183 / 20%), 0 2px 15px 0 rgb(0 0 0 / 5%);
  margin-top: 20px;
}
.no_found {
  max-width: 400px;
  margin: 0 auto;
  text-align: center;
  margin-top: 20px;
}
.no_found h3 {
  margin-bottom: 20px;
  padding-top: 20px;
}
.checkout_button button, .no_found button {
  text-transform: capitalize;
  box-shadow: 0 2px 5px 0 rgba(0,0,0,0.2), 0 2px 10px 0 rgba(0,0,0,0.05);
  font-weight: 500;
}
.cartss {
  position: relative;
  max-width: 29px;
  float: right;
}
.cartss i {
  color: #eb811a;
}
span.link {
  color: #8999a7;
  cursor: pointer;
}
span.link:hover {
  color: orange;
}

.outer_button_wrap button {
  text-transform: capitalize;
  box-shadow: 0 2px 5px 0 rgba(0,0,0,0.2), 0 2px 10px 0 rgba(0,0,0,0.05);
  font-weight: 500;
  width: 100%;
  margin-bottom: 10px;
}

a.cheackout-btn {
  text-transform: capitalize;
  box-shadow: 0 2px 5px 0 rgba(0,0,0,0.2), 0 2px 10px 0 rgba(0,0,0,0.05);
  font-weight: 500;
}
span.span-qnt {
  position: absolute;
  bottom: 0;
  padding: 5px;
  background: #fff;
  border-radius: 50%;
  max-height: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  right: -12px;
  box-shadow: 1px 1px 1px 1px rgba(0,0,0,0.2);
  transition: 0.3s;
  font-size: 12px;
  font-weight: 700;
  width: 20px;
}
.msg {
  font-size: 13px;
  color: #9d8b8b;
}
.formContent {
  border-radius: 10px 10px 10px 10px;
  background: #fff;
  padding: 30px;
  width: 100%;
  max-width: 450px;
  position: relative;
  box-shadow: 0 30px 60px 0 rgb(0 0 0 / 10%);
  text-align: center;
  margin-top: 30px !important;
  margin: 0 auto;
  padding: 30px 15px !important;
}
.cart-img img {
  height: 153px;
  object-fit: contain;
  box-shadow: 1px 1px 5px rgba(0,0,0,0.2);
  padding: 10px;
  border-radius: 12px;
}
button.delete-btn {
  color: #6c757d;
  background: transparent;
  border: transparent;
  cursor: pointer;
  outline: none;
}
.grid {
  display: grid;
  align-items: end;
  align-content: baseline;
  float: right;
  text-align: right;
}
span.total-price-qnt {
  font-size: 15px;
  color: #a3a3a3;
}
h4.title {
  font-size: 17px;
  text-align: center;
  height: 40px;
  overflow: hidden;
}
.qnt-btn button {
  background: #fff;
  margin: 2px;
  border-radius: 4px;
  border: 1px solid orange;
  color: orange;
  outline: none;
  box-shadow: none;
}
.d-flex.qnt-btn {
  justify-content: center;
  align-content: center;
  align-items: baseline;
}
span.price-qnt {
  padding: 10px;
}
footer.mt-4 {
  text-align: center;
  color: #525257;
  padding: 15px;
  margin-top: 40px !important;
}
.inner_cheackout.wrap {
  background: #f7f7f7;
  padding: 30px;
  box-shadow: 1px 1px 11px -3px rgba(0,0,0,0.2);
  margin-top: 20px;
}
.cheack-tic i {
  background: #3cba54;
  color: #fff;
  font-size: 40px;
  padding: 20px;
  border-radius: 50%;
  margin-bottom: 20px;
}
.center.outer_cheackout-wrap {
  max-width: 400px;
  margin: 0 auto;
  text-align: center;
}
h2.thank-you {
  color: #2bbc4f;
  margin-top: 30px;
}
h4.user_name {
  font-size: 15px;
  color: #909090;
}
.form-title h6 {
  font-size: 26px;
  color: #676767;
  padding-bottom: 10px;
}
/*placeholder css*/


.timeline-wrapper h2 {
  text-align: center;
  color: #333;
  font-family: "Roboto", sans-serif;
  font-weight: 300;
}

.timeline-wrapper {
  margin-top: 5%;
}

.timeline-item {
  background: #fff;
  padding: 4px;
  width: 100%;
  display: inline-block;
  max-width: 400px;
}

@-webkit-keyframes placeHolderShimmer {
  0% {
    background-position: -450px 0;
  }
  100% {
    background-position: 450px 0;
  }
}

@keyframes placeHolderShimmer {
  0% {
    background-position: -450px 0;
  }
  100% {
    background-position: 450px 0;
  }
}
.animated-background {
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-name: placeHolderShimmer;
          animation-name: placeHolderShimmer;
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear;
  background: #f6f7f8;
  background: linear-gradient(to right, #f5f4f3 8%, #ebebeb 18%, #f5f4f3 33%);
  background-size: 1200px 104px;
  height: 264px;
  position: relative;
}

.background-masker {
  background: #fff;
  position: absolute;
}

.background-masker.content-top,
.background-masker.content-second-line,
.background-masker.content-third-line,
.background-masker.content-second-end,
.background-masker.content-third-end,
.background-masker.content-first-end {
  top: 204px;
  left: 0;
  right: 0;
  height: 12px;
}

.background-masker.content-top {
  top: 190px;
  height: 14px;
}

.background-masker.content-first-end,
.background-masker.content-second-end,
.background-masker.content-third-end {
  width: auto;
  left: 152px;
  right: 0;
  top: 204px;
  height: 12px;
}

.background-masker.content-second-line {
  top: 216px;
  left: 0;
  right: 0;
  height: 10px;
}

.background-masker.content-second-end {
  left: 171px;
  top: 226px;
}

.background-masker.content-third-line {
  top: 238px;
  height: 14px;
}

.background-masker.content-third-end {
  left: 76px;
  top: 252px;
}



